import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, ConfigLancamentoConhecimentoTransporte } from '../../../../models/dtos/mlf/configLancamentoConhecimentoTransporte';
import { TIPOS_FRETES_CIF_FOB_OUTROS_SEMFRETE_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoFreteEnum';
import { ENTRADA, SAIDA } from '../../../../models/enumerated/mlf/tipoMovimentoEnum';
import { TIPOS_PAGAMENTO_A_VISTA_A_PRAZO_SEM_PAGAMENTO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoPagamentoEnum';
import CFOPIS from '../../../components/ger/cfopIS/CFOPIS';
import EspecieIS from '../../../components/ger/especieIS/EspecieIS';
import SerieIS from '../../../components/ger/serieIS/SerieIS';

interface ConfigLancamentoConhecimentoTransportEditViewProps extends EditViewProps<ConfigLancamentoConhecimentoTransporte> {}

const ConfigLancamentoConhecimentoTransportEditView: React.FC<ConfigLancamentoConhecimentoTransportEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<any>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Configuração de Lançamento de Conhecimento de Transporte">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <EspecieIS id="especie" formProps={{ ...formProps, validation: { required: true } }} />
          </Col>
        </Row>
        <Row>
          <Col>
            <SerieIS id="serie" formProps={formProps} label="Série" placeholder="Informe a Série" />
          </Col>
        </Row>
        <Row>
          <Col>
            <CFOPIS
              formProps={formProps}
              id="cfopEntrada"
              tipoMovimento={ENTRADA.cfops}
              label="CFOP Entrada"
              placeholder="Informe um ou vários CFOP's"
            />
          </Col>
        </Row>

        <Row>
          <Col>
            <CFOPIS formProps={formProps} id="cfopSaida" tipoMovimento={SAIDA.cfops} label="CFOP Saída" placeholder="Informe um ou vários CFOP's" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoFreteInt"
              label="Tipo de Frete"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o tipo de frete"
              inputDropDownProps={{ optionsProvider: TIPOS_FRETES_CIF_FOB_OUTROS_SEMFRETE_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipoPagamentoInt"
              label="Tipo de Pagamento"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o tipo de Pagamento"
              inputDropDownProps={{ optionsProvider: TIPOS_PAGAMENTO_A_VISTA_A_PRAZO_SEM_PAGAMENTO_ENUM_VALUES }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput
              type="switch"
              id="fixarUltimaTransp"
              name="fixarUltimaTransp"
              label="Fixar última transportadora no conhecimento de transporte"
              innerRef={formProps.register}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigLancamentoConhecimentoTransportEditView;
